.lim {
  animation: lim 6s linear infinite alternate;
}

.use {
  animation: us 6s linear .1s infinite alternate;
  left: 100%;
}

@keyframes lim {
  0% {
    right: 0;
  }

  25% {
    right: 0;
  }

  45% {
    right: 0;
  }

  55% {
    right: 100%;
  }

  75% {
    right: 100%;
  }

  100% {
    right: 100%;
  }
}

@keyframes us {
  0% {
    left: 100%;
  }

  25% {
    left: 100%;
  }

  45% {
    left: 100%;
  }

  55% {
    left: 0;
  }

  75% {
    left: 0;
  }

  100% {
    left: 0;
  }
}

